import Bmob from "hydrogen-js-sdk";
import common from '@/utils/common.js';
import customs from '@/utils/customs.js';

import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import uniCalendar from '@/components/uni-calendar/uni-calendar.vue';
import customDetBottom from '@/components/customDetBottom.vue'

let that;
let uid;
export default {
	components: {
		customDetBottom,
		uniCalendar,
		uniSegmentedControl
	},

	data() {
		return {
			items: ['销售情况', '收款记录'],
			current: 0,
			sellType: 0,

			custom: {},
			sellParams: {
				customId: '',
				startDate: common.getDay(0, false),
				endDate: common.getDay(1, false),
				type: -1,
				extra_type: 1,
			}, //获取销售情况参数
			buyTotal: {},//销售情况记录
			getMoney:{},//收款情况记录
		}
	},

	onLoad(options) {
		that = this;
		that.sellParams.customId = options.id;
		uid = uni.getStorageSync('uid');
		//console.log(options.id)
	},
	
	onShow() {
		customs.custom_detail(that.sellParams.customId).then(res => {
			//console.log(res)
			let custom = res
			
			const query = Bmob.Query("order_opreations");
			query.equalTo("type", "==", -1);
			query.equalTo("extra_type", "==", 1);
			query.equalTo("master", "==", uid);
			query.equalTo("custom", "==", that.sellParams.customId);
			query.statTo("sum", "debt");
			query.find().then(res => {
				if(res.length > 0){
					custom.debt = res[0]._sumDebt
				}else{
					custom.debt = 0
				}
				
				that.custom = custom
			})
			
			that.sellParams.customId = res.objectId
			if(that.current == 0){
				customs.getAllCustomSellList(that.sellParams).then(res => {
					that.buyTotal = res
				})
			}else if(that.current == 1){
				customs.getAllCustomInRecord(that.sellParams).then(res => {
					that.getMoney = res
				})
			}
		})
	},
	
	methods: {

		showOptions() {
			uni.showActionSheet({
				itemList: ['销售', '退货'],
				success: function(res) {
					if (res.tapIndex == 0) {
						that.sellType = 0
						that.sellParams.type = -1
						that.sellParams.extra_type = 1
					} else {
						that.sellType = 1
						that.sellParams.type = 1
						that.sellParams.extra_type = 4
					}
					customs.getAllCustomSellList(that.sellParams).then(res => {
						that.buyTotal = res
					})
				}
			});
		},

		open() {
			that.$refs.calendar.open();
		},

		confirm(e) {
			console.log(e)
			if (e.range.data.length == 0) {
				that.sellParams.startDate = e.fulldate
			} else {
				that.sellParams.startDate = e.range.data[0];
				that.sellParams.endDate = e.range.data[e.range.data.length - 1];
			}
			if(that.current == 0){
				customs.getAllCustomSellList(that.sellParams).then(res => {
					console.log(res)
					that.buyTotal = res
				})
			}else if(value == 1){
				customs.getAllCustomInRecord(that.sellParams).then(res => {
					console.log(res)
					that.getMoney = res
				})
			}
		},

		onClickItem(value) {
			that.current = value
			if(that.current == 0){
				customs.getAllCustomSellList(that.sellParams).then(res => {
					console.log(res)
					that.buyTotal = res
				})
			}else if(that.current == 1){
				customs.getAllCustomInRecord(that.sellParams).then(res => {
					console.log(res)
					that.getMoney = res
				})
			}
		},

		//打电话
		makePhoneCall(phone) {
			if (phone) {
				uni.makePhoneCall({
					phoneNumber: phone //仅为示例
				});
			} else {
				uni.showToast({
					icon: "none",
					title: "未填写联系电话"
				})
			}
		},

		show_options() {
			uni.showActionSheet({
				itemList: ["编辑", "删除"],
				success: function(res) {
					if (res.tapIndex == 0) {
						that.edit(that.custom)
					} else if (res.tapIndex == 1) {
						that.delete_this(that.custom.objectId)
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},

		//编辑操作
		edit(item) {
			uni.setStorageSync("customs", item);
			uni.setStorageSync("custom_type", "customs");
			uni.navigateTo({
				url: "../add/add"
			})
		},

		//删除操作
		delete_this(id) {
			uni.showModal({
				title: '提示',
				content: '是否删除此客户',
				success: function(res) {
					if (res.confirm) {
						that.delete_data("customs", id)
					}
				}
			});
		},

		//删除数据
		delete_data(type, id) {
			console.log(id)
			const query = Bmob.Query(type);
			query.destroy(id).then(res => {
				console.log(res)
				uni.showToast({
					title: "删除成功",
					icon: "none"
				})
				uni.navigateBack({
					delta: 1
				})
			}).catch(err => {
				console.log(err)
			})
		},

	},
}
